<template>
  <!-- 歌曲详情页 -->
  <div class="detail-page">
    <div
      class="mask"
      :style="{
        backgroundImage: `url(${
          currentSong.song
            ? currentSong.picUrl
            : currentSong.al
            ? currentSong.al.picUrl
            : currentSong.album.artist.img1v1Url
        }?imageView=1&type=webp&thumbnail=247x0)`,
      }"
    ></div>
    <span
      class="lower iconfont icon-lower"
      @click="$emit('toggle-page', false)"
    ></span>
    <div class="wrapper">
      <img
        class="needle"
        :class="{ paused: !playing }"
        src="https://s3.music.126.net/mobile-new/img/needle-ab.png"
      />
      <div class="record" :class="{ playing: playing }">
        <img
          class="cover"
          :src="
            currentSong.song
              ? currentSong.picUrl
              : currentSong.al
              ? currentSong.al.picUrl
              : currentSong.album.artist.img1v1Url
          "
        />
        <img
          class="disc"
          src="https://s3.music.126.net/mobile-new/img/disc.png"
        />
      </div>
    </div>
    <!--  -->
    <!--  -->
    <div class="box">
      <div class="lyric-wrapper"></div>
      <div class="btm">
        <canvas
          class="canvas"
          width="300"
          height="30"
          ref="progressBar"
        ></canvas>

        <!-- 按钮控件 -->
        <div class="btn">
          <span
            class="iconfont icon-model"
            @click="$emit('toggle-pattern')"
          ></span>
          <span
            class="prev iconfont icon-prev"
            @click="$emit('prev-song')"
          ></span>
          <span
            class="iconfont"
            :class="[playing ? 'icon-play' : 'icon-pause']"
            @click="$emit('toggle-song')"
          ></span>

          <span
            class="next iconfont icon-next"
            @click="$emit('next-song')"
          ></span>
          <span
            class="iconfont icon-playList"
            @click.stop="$emit('toggle-popup', true)"
          ></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
  },
  data() {
    return {
      lyrics: "",
    };
  },
  mounted() {
    this.progressBar = this.$refs.progressBar;
  },
  computed: {
    proportion() {
      return this.currentTime / this.duration;
    },
  },
  watch: {
    proportion(x) {
      var context = this.$refs.progressBar.getContext("2d");
      context.clearRect(0, 20, this.width, this.height);
      context.beginPath();
      context.moveTo(0, 20);
      context.lineTo(300 * x, 20);
      context.strokeStyle = "red";
      context.stroke();

      context.beginPath();
      context.moveTo(0, 20);
      context.lineTo(300, 20);
      context.strokeStyle = "rgba(255, 255, 255, .2)";
      context.stroke();
    },
  },
};
</script>

<style lang="less" scoped>
.detail-page {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 40;
  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    z-index: -2;
  }
  .lower {
    display: inline-block;
    margin-left: 10px;
    margin-top: 10px;
    font-size: 30px;
    color: #fff;
  }
  .mask {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    filter: blur(30px);
    transform: scale(1.5);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    z-index: -1;
  }
  .wrapper {
    margin-top: 40px;
    .needle {
      position: absolute;
      top: 35px;
      left: 50%;
      margin-left: -22px;
      height: 115px;
      transform-origin: 10px 10px;
      transform: rotate(15deg);
      transition: all 1s;
      z-index: 10;
      &.paused {
        transform: rotate(-25deg);
      }
    }
    .record {
      position: relative;
      margin: 0 auto;
      width: 225px;
      height: 225px;
      animation: rotateImg 7s linear infinite;
      animation-play-state: paused;
      img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        &.cover {
          transform: scale(0.8);
        }
      }
      &.playing {
        animation-play-state: running;
      }
    }
  }
  .box {
    position: relative;
    height: 362px;
    overflow: hidden;
    .lyric-wrapper {
      width: 100%;
      height: 285px;
      color: #fff;
      text-align: center;
      background: rgba(0, 0, 0, 0.3);
      .lyric-list {
        height: 100%;
        overflow: hidden;
        li {
          line-height: 32px;
          &:first-child {
            font-size: 18px;
            color: yellow;
            letter-spacing: 2px;
          }
        }
      }
    }
    .btm {
      position: relative;
      height: 75px;
      background: rgba(0, 0, 0, 0.4);
      .canvas {
        position: absolute;
        padding: 0 10%;
        top: -6px;
      }
      .btn {
        position: absolute;
        bottom: 0px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 10%;
        width: 100%;
        span {
          font-size: 20px;
          color: #fff;
          text-shadow: 0px 0px 10px #999;
          &.prev,
          &.next {
            font-size: 25px;
          }
        }
      }
    }
  }
}
</style>